(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('eventEdit', eventEdit);

  /** @ngInject */
  function eventEdit(events) {
    var directive = {
      restrict: 'EA',
      templateUrl: 'app/pages/events/edit/edit.html',
      controller: EventEditController,
      controllerAs: 'eventEdit',
      link: EventEditLink,
      bindToController: true,
      replace: true,
      scope: {
        event: '=eventEdit'
      }
    };

    return directive;

    /** @ngInject */
    function EventEditController(models, matches, $uibModal, $q, utils) {
      var vm = this;

      vm.models = models;
      vm.pointsOptions = utils.scores();

      vm.setLoading = function (res) {
        vm.loading = true;
        return $q.resolve(res);
      }

      vm.unsetLoading = function (res) {
        vm.loading = false;
        return $q.resolve(res);
      }

      vm.submit = function () {
        return vm.setLoading(vm.form)
          .then(events.form2Event)
          .then(function (eventData) {
            return events.update(vm.event.evenId, eventData);
          })
          .finally(vm.unsetLoading);
      }

      vm.publish = function () {
        return vm.setLoading()
          .then(function () {
            return events.update(vm.event.evenId, {even_stato_evento: 1});
          })
          .finally(vm.unsetLoading);
      }

      vm.unpublish = function () {
        return vm.setLoading()
          .then(function () {
            return events.update(vm.event.evenId, {even_stato_evento: 0});
          })
          .finally(vm.unsetLoading);
      }

      vm.recreateCalendar = function () {
        $uibModal.open({
          templateUrl: 'app/pages/events/edit/recreateCalendar.html',
          controller: 'EventRecreateCalendarController',
          controllerAs: 'eventRecreateCalendar',
          windowClass: 'modal-warning'
        }).result
        .then(function (res) {
          if(res === true){
            return vm.setLoading(vm.event.evenId)
              .then(matches.createCalendar)
              .then(function () {
                return events.show(vm.event.evenId);
              })
              .finally(vm.unsetLoading);
          }
        })

      }
    }

    function EventEditLink(scope, el, attrs, vm) {

      el.find('.accordion-body').on('show.bs.collapse', function () {
        events.show(vm.event.evenId);
        events.listRegions();
      });

      vm.cancel = function () {
        el.find('.accordion-body').collapse('hide');
      }

      scope.$watch('eventEdit.event.evenCurrentDay', function (newVal, oldVal) {
        if(angular.isDefined(newVal) && newVal !== oldVal){
          vm.form.currentDay = vm.event.evenCurrentDay;
        }
      });
    }
  }

})();

