(function() {
  'use strict';

  angular
    .module('frispo')
    .controller('MatchAdvancedEditController', MatchAdvancedEditController);

  /** @ngInject */
  function MatchAdvancedEditController($q, $uibModalInstance, match, utils, matches, teams) {
    var vm = this;
    vm.match = match;
    vm.scores = utils.scores();

    vm.ok = function () {
      $uibModalInstance.close(true);
    }

    vm.cancel = function () {
      $uibModalInstance.dismiss();
    }

    vm.submit = function () {
      return vm.setLoading(vm.form)
        .then(matches.form2Match)
        .then(function (matchData) {
          return matches.update(vm.match.matcDayId, matchData);
        })
    }

    vm.setLoading = function (res) {
      vm.loading = true;
      return $q.resolve(res);
    }

    vm.unsetLoading = function (res) {
      vm.loading = false;
      return $q.resolve(res);
    }

    vm.form = {
      matchId: vm.match.matcId,
      teamA: { teamId: vm.match.matcTeamIdA },
      teamB: { teamId: vm.match.matcTeamIdB },
      played: { value: vm.match.matcFlgPlay }
    }

    vm.createAutocompleteObj = function (players) {
      var autocomplete = [];
      if(angular.isArray(players)){
        angular.forEach(players, function (player) {
          autocomplete.push({
            playId: player.playId,
            name: player.playFirstname,
            surname: player.playSurname,
            teamId: player.playTeamId
          });
        });
      }
      return autocomplete;
    }

    vm.autocompleteA = [];
    vm.autocompleteB = [];

    matches.show(vm.match.matcId)
      .then(function () { // Retrieve players for autocomplete
        teams
          .listPlayers(vm.match.matcTeamIdA)
          .then(vm.createAutocompleteObj)
          .then(function (res) {
            vm.autocompleteA = res;
          })
        teams
          .listPlayers(vm.match.matcTeamIdB)
          .then(vm.createAutocompleteObj)
          .then(function (res) {
            vm.autocompleteB = res;
          })
      });
  }
})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('wizardStepBox', wizardStepBox);

  /** @ngInject */
  function wizardStepBox() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/wizard/wizardStepBox/wizardStepBox.html',
      controller: WizardStepBoxController,
      controllerAs: 'wizardStepBox',
      bindToController: true,
      scope: {
        wizard: '=',
        step: '=',
        title: '@'
      },
      link: WizardStepBoxLink,
      transclude: true
    };

    return directive;

    /** @ngInject */
    function WizardStepBoxController() {
    }

    function WizardStepBoxLink(scope, el, attrs, vm) {
      if(vm.step === vm.wizard.currentStep){
        vm.active = true;
      }
      else{
        el.find('.wizard-step-box').first().addClass('collapsed-box');
        el.find('.box-body').first().hide();
      }

      scope.$watch('wizardStepBox.wizard.currentStep', function (newVal, oldVal) {
        if(newVal === oldVal) return;
        // Activate the box
        if(newVal === vm.step && !vm.active){
          vm.active = true;
          angular.element.AdminLTE.boxWidget.collapse(el.find('[data-widget]'));
        }
        // Deactivate the box
        else if(newVal !== vm.step && vm.active){
          vm.active = false;
          angular.element.AdminLTE.boxWidget.collapse(el.find('[data-widget]'));
        }
      })
    }
  }

})();

