(function() {
  'use strict';

  angular
    .module('frispo')
    .controller('CreateEventController', CreateEventController);

  /** @ngInject */
  function CreateEventController(Wizard, events, resPipe, toastr, teams, matches, $log, $state, models, utils) {
    var vm = this;

    vm.models = models;
    // Initialize the wizard object
    vm.wizard = new Wizard({totalSteps: 3, startFromStep: 1});

    // If true, the user have to manually insert the rounds.
    // If false, the user can choose between "Inserimento gironi manuale" and "Inserimento gironi automatico".
    vm.forceManualRounds = true;

    vm.roundNames = utils.alphabetChars();

    vm.initDefaultTeams = function () {
      if(!vm.step2.teams){
        vm.step2.teams = [];
        vm.addTeam();
        vm.addTeam();
      }
    }

    vm.addTeam = function () {
      if(vm.step2 && vm.step2.roundType === 1){
        vm.step2.teams.push({name: ''});
      }
      else{
        vm.step2.teams.push({name: '', round: ''});
      }
    };

    vm.removeTeam = function (index) {
      if(vm.step2.teams.length <= 2){
        toastr.error('E\' obbligatorio inserire almeno 2 squadre', 'Impossibile rimuovere la squadra');
      }
      else{
        vm.step2.teams.splice(index,1);
      }
    }

    vm.step1Done = function () {
      if(!vm.step2) vm.step2 = {}; // Initialize step2 if needed

      // If not "Torneo a gironi"
      if(vm.step1.type != 1){
        vm.step2.roundType = '1';
        vm.initDefaultTeams();
      }

      // If "torneo a gironi" and user can't choose manual or automatic round type
      else if(vm.forceManualRounds){
        vm.step2.roundType = '0'; // Manual rounds
        vm.initDefaultTeams();
      }

      vm.wizard.next();
    }

    vm.submitStep1 = function () {
      $log.info('creando l\'evento..');
      return events
        .form2Event(vm.step1)
        .then(events.create);
    }

    vm.step2Done = function () {
      vm.wizard.next();
    }

    vm.submitStep2 = function () {
      $log.info('creando le squadre..');
      return teams
        .form2Teams(vm.step2)
        .then(teams.addGroup);
    }

    vm.step3Done = function () {
      vm.submitWizard();
    }

    vm.submitStep3 = function () {
      $log.info('creando il calendario..');
      return matches.createCalendar(models.currentEvent.evenId);
    }

    vm.submitWizard = function () {
      vm.wizard.loading = true;
      vm.submitStep1()
        .then(vm.submitStep2)
        .then(vm.submitStep3)
        .then(function () {
          $state.go('main.admin.events');
        })
        .finally(function () {
          vm.wizard.loading = false;
        });
    }

    vm.manualSubmit = function () {
      vm.wizard.loading = true;
      vm.submitStep1()
        .then(vm.submitStep2)
        .then(function () {
          $state.go('main.admin.events');
        })
        .finally(function () {
          vm.wizard.loading = false;
        })
    }
  }
})();

