'use strict';

angular.module('frispo')
  /**
   * A wizard
   */
  .factory('Wizard', function () {
    var defaults = {
      totalSteps: 1,
      startFromStep: 1
    }

    return function Wizard (options) {
      this.options = angular.merge(defaults, options);

      this.totalSteps = this.options.totalSteps;
      this.currentStep = this.options.startFromStep;

      this.next = function () {
        if(this.currentStep < this.totalSteps){
          this.currentStep++;
        }
      }

      this.prev = function () {
        if(this.currentStep > 1){
          this.currentStep--;
        }
      }
    };
  });

