'use strict';

angular.module('frispo')
  /**
   * Error Manager.
   * Handles common HTTP error messages
   */
  .factory('errorManager', function (toastr, $log, $state, $q, longTermMemory, models) {
    var errorManager = function(err) {
      // log every errors
      $log.error(err);

      // Unwrap HTTP errors and take actions if needed
      if(angular.isObject(err) && err.status){
        switch(err.status){
          case 401:
            $state.go('main.logout');
            err = err.data;
            break;
          case 403:
            $state.go('main.home');
            err = err.data;
            break;
          case 422:
            err = err.data;
            break;
          case 404:
            err = err.data;
            break;
          case 413:
            err = 'Il file che stai cercando di caricare  troppo grande';
            break;
          case -1:
            err = 'Il server non  raggiungibile o non sei connesso ad Internet';
            break;
          default:
            break;
        }
      }

      var getErrorString = function (err) {
        if(angular.isString(err)){
          return err;
        }
        else if(angular.isObject(err) && (err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error)){
          return getErrorString(err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error );
        }
        else if(angular.isArray(err)){
          var myErr = [];
          for (var i = 0; i < err.length; i++) {
            myErr[i] = getErrorString(err[i]);
          }
          return getErrorString(myErr.join('<br>'));
        }
        // Don't manage different kinds of error
        else{
          longTermMemory.oblivion(); // Forget everything. We don't want the app to be broken forever!
          models.reset(); // Reset all the models. We don't want the app to be broken forever!
          return 'Si  verificato un errore imprevisto. Si prega di riprovare pi tardi.';
        }
      }

      toastr.error(getErrorString(err), 'Errore');

      return $q.reject(err);
    };

    return errorManager;
  });

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('datepicker', datepicker);

  /** @ngInject */
  function datepicker() {
    var directive = {
      restrict: 'A',
      link: datepickerLink,
      scope: {
        datepicker: '@',
        model: '='
      }
    };

    return directive;

    /** @ngInject */
    function datepickerLink(scope, el) {
      scope.watchInput = function () {
        scope.$watch('model', function () {
          scope.drp.elementChanged();
        });
      }

      if(scope.datepicker === 'range'){
        scope.drp = el.daterangepicker({
          autoUpdateInput: false,
          locale: {
            format: 'DD/MM/YYYY',
            separator: ' -> '
          }
        }).data('daterangepicker');
        el.on('apply.daterangepicker', function (ev, picker) {
          scope.$apply(function () {
            scope.model = picker.startDate.format('DD/MM/YYYY') + ' -> ' + picker.endDate.format('DD/MM/YYYY');
          })
        })
        el.on('cancel.daterangepicker', function () {
          scope.$apply(function () {
            scope.model = '';
          })
        })
        scope.watchInput();
      }
      else{
        scope.drp = el.daterangepicker({
          autoUpdateInput: false,
          singleDatePicker: true,
          locale: {
            format: 'DD/MM/YYYY',
            separator: ' -> '
          }
        }).data('daterangepicker');
        el.on('apply.daterangepicker', function (ev, picker) {
          scope.$apply(function () {
            scope.model = picker.startDate.format('DD/MM/YYYY');
          })
        })
        el.on('cancel.daterangepicker', function () {
          scope.$apply(function () {
            scope.model = '';
          })
        })
        scope.watchInput();
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('compareTo', compareTo);

  /** @ngInject */
  function compareTo() {
    var directive = {
      restrict: 'A',
      require: 'ngModel',
      scope: {
        otherModelValue: '=compareTo'
      },
      link: compareToLink
    };

    return directive;

    /** @ngInject */
    function compareToLink(scope, element, attrs, ngModel) {
      ngModel.$validators.compareTo = function (modelvalue) {
        return modelvalue == scope.otherModelValue;
      }
      scope.$watch('otherModelValue', function () {
        ngModel.$validate();
      })
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .directive('can', canDirective);

  /** @ngInject */
  function canDirective($log, models) {
    var directive = {
      restrict: 'A',
      link: linkFunc,
      scope: {
        can: '@'
      }
    };

    return directive;

    function linkFunc(scope, el) {
      var watcher;
      var shown;
      var negative;
      var permission;
      scope.models = models;

      // Update the visibility of the element
      var updateVisibility = function() {
        var toShow = false;
        if(angular.isArray(models.currentUser.permissions)){ // look for permission
            for (var i = 0; i < models.currentUser.permissions.length; i++) {
              if(models.currentUser.permissions[i].permName === permission){
                toShow = true;
                break;
              }
            }
        }
        if(negative){ toShow = !toShow; } // invert the condition if needed

        // Show the element if not shown
        if(toShow && !shown){
          el.removeClass('can-hide');
          shown = true;
        }

        // Hide the element if shown
        else if(!toShow && shown){
          el.addClass('can-hide');
          shown = false;
        }
      };

      // If the action starts with "!", the action is negative
      negative = scope.can.slice(0, 1) === '!';


      // In that case, remove ! from the string
      if(negative){
        permission = scope.can.substr(1);
      }
      else{
        permission = scope.can;
      }

      // Update the visibility for the first time
      shown = true;
      updateVisibility();

      // Update the visibility as soon as the permissions change
      watcher = scope.$watch('models.currentUser.permissions', function(newVal, oldVal) {
        if(newVal && oldVal !== newVal){
          updateVisibility();
        }
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }
  }

})();

