'use strict';

angular.module('frispo')
  /**
   * User models manager
   */
   .factory('users', function (frispoApi, errorManager, models, longTermMemory, resPipe, $state, $q, ezfb, $log, uiState, $timeout) {

      var users = {
        /**
         * Register a new user
         * @param  {Object} userData an object containing 'email' and 'password'
         * @return {Promise}         a promise with the authentication data for the user
         */
        register: function (userData) {
          return frispoApi
            .register(userData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(function (res) {
              res.email = userData.email;
              return res;
            })
            .then(resPipe.camelize)
            .then(resPipe.injectUserDefaults)
            .then(resPipe.updateCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .catch(errorManager);
        },

        /**
         * Login a user
         * @param  {Object} userData an object containing 'email' and 'password'
         * @return {Promise}         a promise with the authentication data for the user
         */
        login: function (userData) {
          return frispoApi
            .login(userData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(function (res) {
              res.email = userData.email; // Add the email from the input as it's missing from the response
              return res;
            })
            .then(resPipe.camelize)
            .then(resPipe.injectUserDefaults)
            .then(resPipe.updateCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .then(users.onLoggedIn)
            .catch(errorManager);
        },

        /**
         * Logout a user
         * @param  {Object} userData an object containing the user data
         * @return {Promise}          a promise that will be fullfilled after the logout
         */
        logout: function () {
          if(models.currentUser && models.currentUser.userToken){
            return frispoApi
              .logout()
              .then(function (res) {
                $log.info('Frispo logout');
                return res;
              })
              .catch(errorManager)
              .finally(users.onLoggedOut)
              .then(function () {
                ezfb.logout();
              });
          }
          return $q.resolve();
        },

        /**
         * Login a user to frispo's server using the facebook login() response data
         * @param  {Object} facebookData The object returned by the Facebook SDK after a succesful login
         * @return {Promise}             A promise fulfilled with the authentication data for the user,
         */
        loginFacebook: function (facebookData) {
          return frispoApi
            .loginFacebook(facebookData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectUserDefaults)
            .then(resPipe.updateCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .then(users.onLoggedIn)
            .catch(errorManager);
        },

        /**
         * Fetch all user info
         */
        info: function () {
          var infoCalls = [
            users.showPreferences(),
            users.show()
          ];
          return $q.all(infoCalls);
        },

        /**
         * Show basic  user info
         */
        show: function () {
          return frispoApi
            .showUser()
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.updateMergeCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .catch(errorManager);
        },

        /**
         * Edit basic user info
         */
        update: function (userData) {
          return frispoApi
            .updateUser(userData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.updateMergeCurrentUser)
            .then(resPipe.memorizeCurrentUser)
            .catch(errorManager);
        },

        /**
         * Get the preferences of the user
         */
        showPreferences: function () {
          return frispoApi
            .showPreferences()
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.updatePreferences)
            .then(resPipe.memorizeCurrentUser)
            .catch(errorManager);
        },

        /**
         * This function is used as a callback whenever a user has been succesfully logged in
         * @param  {Object} userData The object containing the user data
         * @return {Object}          The same userData, useful for promise chaining
         */
        onLoggedIn: function (userData) {
          if($state.current.name === 'main.access'){
            $state.go('main.admin.events');
          }
          $log.info('Frispo login');
          users.info();
          uiState.loggedIn = true;
          return $q.resolve(userData);
        },

        /**
         * This function is used as a callback whenever a user has been succesfully logged out
         */
        onLoggedOut: function() {
          models.reset();
          longTermMemory.oblivion();
          uiState.loggedIn = false;
          if($state.current.name !== 'main.login' && $state.current.name !== 'main.register'){
            $timeout(function () {
              $state.go('main.login');
            })
          }
        },

        /**
         * Backup user data and setup the anonymous user for testing purposes
         */
        setAnonymousUser: function (res) {
          models.bkp =  {
            uuid: models.currentUser.uuid,
            accessToken: models.currentUser.accessToken
          }
          models.currentUser.uuid = 'anonimo';
          models.currentUser.accessToken = '4e66c966e021918f0b088ddc929847f7'
          return $q.resolve(res);
        },

        /**
         * Restore user backup
         */
        unsetAnonymousUser: function (res) {
          models.currentUser.uuid = models.bkp.uuid;
          models.currentUser.accessToken = models.bkp.accessToken;
          delete models.bkp;
          return $q.resolve(res);
        },

        checkFacebookLogin: function () {
          return ezfb
            .getLoginStatus()
            .then(function (res) {
              if(res.status === 'connected'){
                return res;
              }
              else{
                return $q.reject(res);
              }
            });
        },

        /**
         * Configure the facebook login and events
         */
        configureFacebookCallbacks: function (res) {
          // Subscribe to FB login
          ezfb.Event.subscribe('auth.login', function (response) {
            $log.info('Facebook login');
            users
              .loginFacebook(response)
              .then(function () {
                $state.go('main.access');
              });
          });

          // Subscribe to FB logout
          ezfb.Event.subscribe('auth.logout', function () {
            $log.info('Facebook logout');
            if(uiState.loggedIn){
              users.logout();
            }
          });


          return res;
        },

        form2Team: function (formData) {
          var userData = {};
          if(angular.isDefined(formData.type)) userData.type = formData.type;
          if(angular.isDefined(formData.subscription)) userData.subscription = formData.subscription;
          return $q.resolve(userData);
        }
      };

      return users;
   });

