'use strict';

angular.module('frispo')
  /**
  * Models cache
  */
  .factory('models', function (longTermMemory) {

    var models = {
       reset: function () {
         models.events = longTermMemory.remember('events') || [];
         models.regions = longTermMemory.remember('regions') || [];
         models.currentUser = longTermMemory.remember('currentUser') || {};
         models.currentEvent = models.events.length>0 && longTermMemory.remember('currentEvent') || undefined;
       }
    };

    models.reset();

    return models;
  });

'use strict';

angular.module('frispo')
  /**
   * Matches models manager
   */
   .factory('matches', function (frispoApi, errorManager, resPipe, models, utils, $q) {

      var matches = {

        /**
         * Create a matches calendar for an event
         */
        createCalendar: function (eventId) {
          return frispoApi
            .createMatchesCalendar(eventId)
            .catch(errorManager);
        },

        /**
         * List the matches for a day of an event
         */
        list: function () {
          return frispoApi
            .listMatches(models.currentEvent.evenId, models.currentEvent.currentDay.matcDayId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.injectMatchDefaults)
            .then(resPipe.updateMatches)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * List the scorers of a match
         */
        listScorers: function (matchId) {
          return frispoApi
            .listMatchScorers(models.currentEvent.evenId, matchId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(function (res) {
              return resPipe.updateMatchScorers(matchId, res);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Create a scorer
         */
        createScorer: function (matchId, scorerData, scorersKey) {
          return frispoApi
            .createScorer(models.currentEvent.evenId, matchId, scorerData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(function (res) {
              resPipe.insertScorer(matchId, res, scorersKey);
              return res;
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Create a card
         */
        createCard: function (matchId, cardData, cardsKey) {
          return frispoApi
            .createCard(models.currentEvent.evenId, matchId, cardData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectCardsDefaults)
            .then(function (res) {
              resPipe.insertCard(matchId, res, cardsKey);
              return res;
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Destroy a scorer
         */
        destroyScorer: function (matchId, playerId, scorersKey) {
          return frispoApi.destroyScorerMatch(models.currentEvent.evenId, playerId, matchId)
            .then(resPipe.parseResponse)
            .then(function () {
              return resPipe.removeScorer(matchId, playerId, scorersKey);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Destroy a card
         */
        destroyMatchCard: function (matchId, cardId, cardsKey) {
          return frispoApi.destroyCard(models.currentEvent.evenId, matchId, cardId)
            .then(resPipe.parseResponse)
            .then(function () {
              return resPipe.removeMatchCard(matchId, cardId, cardsKey);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Destroy a card
         */
        destroyEventCard: function (matchId, playerId, cardId, cardsKey, cardMatchKey) {
          return frispoApi
            .destroyCard(models.currentEvent.evenId, matchId, cardId)
            .then(resPipe.parseResponse)
            .then(function () {
              return resPipe.removeEventCard(matchId, playerId, cardId, cardsKey, cardMatchKey);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Update a card
         */
        updateEventCard: function (matchId, playerId, cardId, cardsKey, cardMatchKey, cardData) {
          return frispoApi
            .updateCard(models.currentEvent.evenId, matchId, cardId, cardData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectCardsDefaults)
            .then(function (res) {
              return resPipe.updateEventCard(matchId, playerId, cardId, cardsKey, cardMatchKey, res);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Update a match
         */
        update: function (matchDayId, matchData) {
          return frispoApi
            .updateMatch(models.currentEvent.evenId, matchData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectMatchDefaults)
            .then(function (res) {
              res.matcDayId = matchDayId; // Inject the match day ID, as the server returns 0
              return res;
            })
            .then(resPipe.updateMatch)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Create a match
         */
        create: function (matchData) {
          return frispoApi
            .createMatch(models.currentEvent.evenId, models.currentEvent.currentDay.matcDayId, matchData)
            .then(resPipe.parseResponse)
            .then(resPipe.log)
            .catch(errorManager);
        },

        /**
         * Create a match day
         */
        createDay: function (dayData) {
          return frispoApi
            .createDay(models.currentEvent.evenId, dayData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.insertMatchDay)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Destroy a match
         */
        destroy: function (matchData) {
          return frispoApi
            .destroyMatch(models.currentEvent.evenId, models.currentEvent.currentDay.matcDayId, matchData.matcId)
            .then(resPipe.parseResponse)
            .then(function () {
              return matchData;
            })
            .then(resPipe.removeMatch)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Show a match
         */
         show: function (matchId) {
           return frispoApi
            .showMatch(models.currentEvent.evenId, matchId)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.updateMatch)
            .then(resPipe.injectMatchDefaults)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
         },

         /**
          * Translate data from a match form into a matchData object
          * @param  {Object} formData The form input
          * @return {Promise}         A promise fulfilled with the matchData object
          */
         form2Match: function (formData) {
           var matchData = {};
           var matchTime = utils.str2Time(formData.time);
           var matchDate = utils.str2Date(formData.date);
           if(angular.isDefined(formData.matchId)) matchData.matc_id = formData.matchId;
           if(formData.teamA && angular.isDefined(formData.teamA.teamId)) matchData.matc_team_id_a = formData.teamA.teamId;
           if(formData.teamB && angular.isDefined(formData.teamB.teamId)) matchData.matc_team_id_b = formData.teamB.teamId;
           if(angular.isDefined(formData.scoreA)) matchData.matc_goal_team_a = formData.scoreA;
           if(angular.isDefined(formData.scoreB)) matchData.matc_goal_team_b = formData.scoreB;
           if(angular.isString(formData.dateExpected)) matchData.matc_date_expected = formData.dateExpected;
           if(angular.isString(matchDate)) matchData.matc_date_match = matchDate;
           if(angular.isString(matchTime)) matchData.matc_time = matchTime;
           if(angular.isString(formData.address)) matchData.matc_address = formData.address;
           if(angular.isString(formData.round)) matchData.matc_name_groups = formData.round;
           if(formData.played && angular.isDefined(formData.played.value)) matchData.matc_flg_play = formData.played.value;
           if(angular.isDefined(formData.shootoutA)) matchData.matc_shootout_team_a = formData.shootoutA;
           if(angular.isDefined(formData.shootoutB)) matchData.matc_shootout_team_b = formData.shootoutB;
           if(angular.isString(formData.place)) matchData.matc_place = formData.place;
           return $q.resolve(matchData);
         },

         /**
          * Translate data from a scorer form into a scorerData object
          * @param  {Object} formData The form input
          * @return {Promise}         A promise fulfilled with the scorerData object
          */
         form2Scorer: function (formData) {
           var scorerData = {};
           if(angular.isDefined(formData.teamId)) scorerData.team_id = ''+formData.teamId;
           if(angular.isDefined(formData.playId)) scorerData.play_id = ''+formData.playId;
           else scorerData.play_id = '0';
           if(angular.isDefined(formData.goals)) scorerData.goal = formData.goals;
           if(angular.isDefined(formData.name)) scorerData.play_name = formData.name;
           if(angular.isDefined(formData.surname)) scorerData.play_surname = formData.surname;
           return $q.resolve(scorerData);
         },

         /**
          * Translate data from a card form into a card object
          * @param  {Object} formData The form input
          * @return {Promise}         A promise fulfilled with the cardData object
          */
         form2CardMatch: function (formData) {
           var cardData = {};
           if(angular.isDefined(formData.playId)) cardData.play_id = formData.playId;
           if(angular.isDefined(formData.typeSqualifications)) cardData.flg_cards = formData.typeSqualifications.value;
           if(angular.isDefined(formData.daySqualifications)) cardData.day_squalification = formData.daySqualifications.value;
           if(angular.isDefined(formData.note)) cardData.note = formData.note;
           if(angular.isDefined(formData.squalificationStatus)) cardData.squalification_status = formData.squalificationStatus.value;
           return $q.resolve(cardData);
         },

         /**
          * Translate data from a card form into a cardData object
          * @param  {Object} formData The form input
          * @return {Promise}         A promise fulfilled with the cardData object
          */
         form2Card: function (formData) {
           var cardData = {};
           if(angular.isDefined(formData.teamId)) cardData.team_id = ''+formData.teamId;
           if(angular.isDefined(formData.playId)) cardData.play_id = ''+formData.playId;
           else cardData.play_id = '0';
           if(angular.isDefined(formData.cardType) && angular.isDefined(formData.cardType.id)) cardData.flg_cards = ''+formData.cardType.id;
           if(angular.isDefined(formData.disqualificationDays)) cardData.day_squalification = ''+formData.disqualificationDays;
           if(angular.isDefined(formData.note)) cardData.note = ''+formData.note;
           if(angular.isDefined(formData.name)) cardData.play_name = ''+formData.name;
           if(angular.isDefined(formData.surname)) cardData.play_surname = ''+formData.surname;
           return $q.resolve(cardData);
         },

         /**
          * Translate data from a Day form into an dayData object
          * @param  {Object} formData The form input
          * @return {Promise}         A promise fulfilled with the dayData object
          */
          form2Day: function (formData) {
           var dayData = {};
           if(angular.isDefined(formData.name)) dayData.mada_name = formData.name;
           if(angular.isDefined(formData.desc)) dayData.mada_description = formData.desc;
           return $q.resolve(dayData);
         }
      };

      return matches;
   });

