'use strict';

angular.module('frispo')
  /**
   * Event models manager
   */
   .factory('events', function (frispoApi, errorManager, resPipe, $q, utils) {

      var events = {
        /**
         * Show all events
         */
         list: function () {
          return frispoApi
            .listEvents()
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.injectEventsDefaults)
            .then(resPipe.updateEvents)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Show all events managed by a user
         */
        listManaged: function () {
          return frispoApi
            .listManagedEvents()
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.injectEventsDefaults)
            .then(resPipe.updateManagedEvents)
            .then(resPipe.memorizeCurrentUser)
            .catch(errorManager);
        },

        /**
         * Create a new event
         */
        create: function (eventData) {
          return frispoApi
            .createEvent(eventData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectEventsDefaults)
            .then(resPipe.updateCurrentEvent)
            .then(resPipe.memorizeCurrentEvent)
            .catch(errorManager);
        },

        /**
         * Show an event
         */
        show: function (eventId) {
          return frispoApi
            .showEvent(eventId)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectEventsDefaults)
            .then(resPipe.updateCurrentEvent)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Show the scoreboard of an event
         */
        showScoreboard: function (event) {
          var apiCall;
          // Torneo a gironi
          if(event.evenType === 1){
            apiCall = frispoApi.showTournamentScoreboard(event.evenId);
          }
          else{
            apiCall = frispoApi.showEventScoreboard(event.evenId);
          }
          return apiCall
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.updateScoreboard)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Show the scorers of an event
         */
        showScorers: function (eventId) {
          return frispoApi.showScorers(eventId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.updateScorers)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        destroyScorerMatch: function (eventId, playerId, scorerMatch) {
          return frispoApi.destroyScorerMatch(eventId, playerId, scorerMatch.matcId)
            .then(resPipe.parseResponse)
            .then(function () {
              return resPipe.removeScorerMatch(playerId, scorerMatch);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        updateScorerMatch: function (eventId, matchId, scorerMatchData) {
          return frispoApi.updateScorerMatch(eventId, matchId, scorerMatchData)
            .then(resPipe.parseResponse)
            .then(function () { // Mock a response
              return {
                matcId: matchId,
                matcScorerGoal: scorerMatchData.scor_goal
              };
            })
            .then(function (res) {
              return resPipe.updateScorerMatch(scorerMatchData.scor_play_id, res);
            })
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
         * Destroy an event
         */
        destroy: function (event) {
          return frispoApi
            .destroyEvent(event)
            .then(resPipe.parseResponse)
            .then(function () {
              return event;
            })
            .then(resPipe.removeEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager)
        },

        /**
         * Update an event
         */
        update: function (eventId, eventData) {
          return frispoApi
            .updateEvent(eventId, eventData)
            .then(resPipe.parseResponse)
            .then(resPipe.unwrapArray)
            .then(resPipe.camelize)
            .then(resPipe.injectEventsDefaults)
            .then(resPipe.updateCurrentEvent)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .catch(errorManager);
        },

        /**
        * List the active regions
        */
        listRegions: function () {
          return frispoApi
            .listRegions()
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.updateRegions)
            .then(resPipe.memorizeRegions)
            .catch(errorManager);
        },

        /**
         * List the yellow cards for the event
         */
        listYellows: function (eventId) {
          return frispoApi
            .listYellowCards(eventId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.injectEventCardsDefaults)
            .then(resPipe.updateYellows)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .then(resPipe.log)
            .catch(errorManager)
        },

        /**
         * List the diffides for the event
         */
        listDiffides: function (eventId) {
          return frispoApi
            .listDiffides(eventId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.injectEventCardsDefaults)
            .then(resPipe.updateDiffides)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .then(resPipe.log)
            .catch(errorManager)
        },

        /**
         * List the squalifications for the event
         */
        listSqualifications: function (eventId) {
          return frispoApi
            .listSqualifications(eventId)
            .then(resPipe.parseResponse)
            .then(resPipe.camelize)
            .then(resPipe.injectEventCardsDefaults)
            .then(resPipe.updateSqualifications)
            .then(resPipe.memorizeCurrentEvent)
            .then(resPipe.memorizeEvents)
            .then(resPipe.log)
            .catch(errorManager)
        },

        /**
         * Translate data from an event form into an eventData object
         * @param  {Object} formData The form input
         * @return {Promise}         A promise fulfilled with the eventData object
         */
        form2Event: function (formData) {
          var eventData = {};
          var dateRange = utils.str2Date(formData.dates);
          if(angular.isDefined(formData.name)) eventData.even_name = formData.name;
          if(angular.isDefined(formData.desc)) eventData.even_description = formData.desc;
          if(angular.isDefined(formData.email)) eventData.even_email = formData.email;
          if(angular.isDefined(formData.address)) eventData.even_address = formData.address;
          if(angular.isDefined(formData.type)) eventData.even_type = '' + formData.type;
          if(angular.isDefined(formData.organizer)) eventData.even_organize_by = formData.organizer;
          if(angular.isDefined(formData.formula)) eventData.even_formula = '' + formData.formula;
          if(angular.isDefined(formData.telephone)) eventData.even_mobile_phone = formData.telephone;
          if(angular.isDefined(formData.yellowPoints)) eventData.even_yellow_points = formData.yellowPoints;
          if(angular.isDefined(formData.redPoints)) eventData.even_red_points = formData.redPoints;
          if(angular.isDefined(formData.yellowDiffide)) eventData.even_yellow_diffide = formData.yellowDiffide;
          if(angular.isDefined(formData.yellowDiffide2)) eventData.even_yellow_diffide2 = formData.yellowDiffide2;
          if(angular.isArray(dateRange) && dateRange.length > 0 && dateRange[0]) eventData.even_date_start = dateRange[0];
          if(angular.isArray(dateRange) && dateRange.length > 1 && dateRange[1]) eventData.even_date_end = dateRange[1];
          if(formData.region && formData.region.regiId) eventData.regi_id = formData.region.regiId;
          // TODO: remove this line: it's a mock
          else eventData.regi_id = 1;
          if(formData.currentDay && formData.currentDay.matcDayId) eventData.even_day_active = formData.currentDay.matcDayId;
          return $q.resolve(eventData);
        }

      };

      return events;
   });

(function() {
  'use strict';

  angular
    .module('frispo')
    .run(runBlock);

  /** @ngInject */
  function runBlock(
    $rootScope,
    uiState,
    defaultBodyClasses,
    models,
    users
  ) {
      // Bind the uiState to $rootScope as it's used in index.html
      $rootScope.uiState = uiState;

      // On state change
      var stateChangeSuccess = $rootScope.$on('$stateChangeSuccess', function (event, toState) {
        var pms = toState.params || {};
        uiState.bodyClasses = pms.bodyClasses || defaultBodyClasses;
      });

      // Update user info (if logged in)
      if(models.currentUser && models.currentUser.userToken){
        users.info();
      }

      // Destroy watchers
      $rootScope.$on('$destroy', function() {
        stateChangeSuccess();
      });
  }
})();

(function() {
  'use strict';

  angular
    .module('frispo')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    var requireLoggedIn = function ($timeout, $state, models) {
      if(!models.currentUser || !models.currentUser.userToken){
        $timeout(function() {
          $state.go('main.access');
        });
      }
    };

    $stateProvider
      .state('main', {
        abstract: true,
        templateUrl: 'app/pages/main/main.html'
      })
      .state('main.admin', {
        abstract: true,
        templateUrl: 'app/pages/admin/admin.html',
        onEnter: requireLoggedIn
      })
      .state('main.access', {
        url: '/',
        templateUrl: 'app/pages/access/access.html',
        controller: 'AccessController',
        controllerAs: 'vm'
      })
      .state('main.admin.profile', {
        url: '/profile',
        templateUrl: 'app/pages/profile/profile.html',
        controller: 'ProfileController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.createEvent', {
        url: '/create-event',
        templateUrl: 'app/pages/createEvent/createEvent.html',
        controller: 'CreateEventController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.events', {
        url: '/events',
        templateUrl: 'app/pages/events/events.html',
        controller: 'EventsController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.teams', {
        url: '/teams',
        templateUrl: 'app/pages/teams/teams.html',
        controller: 'TeamsController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.players', {
        url: '/players',
        templateUrl: 'app/pages/players/players.html',
        controller: 'PlayersController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.calendar', {
        url: '/calendar',
        templateUrl: 'app/pages/calendar/calendar.html',
        controller: 'CalendarController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.scoreboard', {
        url: '/scoreboard',
        templateUrl: 'app/pages/scoreboard/scoreboard.html',
        controller: 'ScoreboardController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.scorers', {
        url: '/scorers',
        templateUrl: 'app/pages/scorers/scorers.html',
        controller: 'ScorersController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.press', {
        url: '/press',
        templateUrl: 'app/pages/press/press.html',
        controller: 'PressController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.invitations', {
        url: '/invitations',
        templateUrl: 'app/pages/invitations/invitations.html',
        controller: 'InvitationsController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.disciplinary', {
        url: '/disciplinary',
        templateUrl: 'app/pages/disciplinary/disciplinary.html',
        controller: 'DisciplinaryController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.managerEvents', {
        url: '/manager-events',
        templateUrl: 'app/pages/managerEvents/managerEvents.html',
        controller: 'ManagerEventsController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.admin.managerMatches', {
        url: '/manager-matches',
        templateUrl: 'app/pages/managerMatches/managerMatches.html',
        controller: 'ManagerMatchesController',
        controllerAs: 'vm',
        onEnter: requireLoggedIn
      })
      .state('main.login', {
        url: '/login',
        templateUrl: 'app/pages/login/login.html',
        controller: 'LoginController',
        controllerAs: 'vm',
        params: {
          bodyClasses: 'hold-transition login-page'
        },
        onEnter: function ($timeout, $state, uiState) {
          if(uiState.loggedIn){
            $timeout(function () {
              $state.go('main.access');
            })
          }
        }
      })
      .state('main.register', {
        url: '/register',
        templateUrl: 'app/pages/register/register.html',
        controller: 'RegisterController',
        controllerAs: 'vm',
        params: {
          bodyClasses: 'hold-transition register-page'
        },
        onEnter: function ($timeout, $state, uiState) {
          if(uiState.loggedIn){
            $timeout(function () {
              $state.go('main.access');
            })
          }
        }
      })
      .state('main.logout', {
        url: '/logout',
        onEnter: function($timeout, users) {
          $timeout(function () {
            users.logout();
          })
        }
      });
    $urlRouterProvider.otherwise('/');
  }

})();

